<template>
  <div class="monster-compass">
    <div v-if="isShowCompass" class="compass-box">
      <!-- 林開群 start -->
      <div v-if="!isLingWin" @click="showLingMap" class="compass-ling">林開群</div>
      <!-- 林開群 end -->
      <img class='compass-logo' src='@/assets/images/compass-scan.png' alt='scan'>
      <div class="compass-head">怪奇羅盤</div>
      <div class="compass-text">請使用地圖 QRcode 前往怪奇地圖</div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'MonsterCompass',
  components: {

  },
  data () {
    return {
      isShowCompass: true,
      isShowLing: false,
      isShowBackMap: false,
      isShowLingMan: false,
      isShowLingQuestion: false,
      lingAnswer: '',
      isEnterLing: true
    }
  },
  mounted () {

  },
  destroyed () {

  },
  methods: {
    showLingMap () {
      if (this.isOnlyFightLing) {
        this.$router.push({
          path: './haunted-house-fight'
        })
      } else {
        this.$router.push({
          path: './haunted-house-compass'
        })
      }
    }
  },
  computed: {
    isLingWin () {
      return this.$store.state.isLingWin
    },
    isOnlyFightLing () {
      return this.$store.state.isOnlyFightLing
    }
  }
}
</script>
<style lang="scss" scoped>

  .monster-compass {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);

    .compass {

      &-box {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
      }

      &-ling {
        color: #fff;
        font-size: 30px;
        margin-top: 20px;
      }

      &-logo {
        width: 200px;
      }

      &-head {
        font-size: 20px;
        color: #95ff00;
        margin-top: 10px;
      }

      &-text {
        color: #fff;
        margin-top: 4px;
      }
    }
  }

</style>



